<template>
  <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <h3 class="title">诊断系统</h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-select  prop="loginType" v-model="ruleForm2.loginType" placeholder="请选择账户类型" style="width:100%;">
      <el-option
       v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    <!-- <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox> -->
    <el-form-item style="width:100%;margin-top:30px;">
      <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining">登录</el-button>
      <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
    </el-form-item>
  </el-form>
</template>

<script>
    import {
        requestLogin
    } from '../api/token';
    import {
        getMenuTree
    } from '../api/menu'
    import NProgress from 'nprogress'
    import MenuUtils from '../utils/MenuUtils'
    var routers = []
    export default {
        data() {
            return {
                logining: false,
                ruleForm2: {
                    account: '',
                    checkPass: '',
                },
                options: [{
                    value: 'hr',
                    label: '管理員'
                }, {
                    value: 'patient',
                    label: '患者'
                }],
                rules2: {
                    account: [{
                            required: true,
                            message: '请输入账号',
                            trigger: 'blur'
                        },
                        //{ validator: validaePass }
                    ],
                    checkPass: [{
                            required: true,
                            message: '请输入密码',
                            trigger: 'blur'
                        },
                        //{ validator: validaePass2 }
                    ],
                    loginType: [{
                            required: true,
                            message: '请选择账户类型',
                            trigger: 'blur'
                        },
                        //{ validator: validaePass2 }
                    ]
                },
                // checked: true
            };
        },
        methods: {
            handleReset2() {
                this.$refs.ruleForm2.resetFields();
            },
            login(data) {
                window.sessionStorage.setItem('menus', JSON.stringify(data))
                MenuUtils(routers, data)
            },
            handleSubmit2(ev) {
                var _this = this;
                this.$refs.ruleForm2.validate((valid) => {
                    if (valid) {
                        this.logining = true;
                        NProgress.start();
                        var loginParams = {
                            username: this.ruleForm2.account,
                            password: this.ruleForm2.checkPass,
                            loginType: this.ruleForm2.loginType
                        };
                        requestLogin(loginParams).then(res => {
                            this.logining = false;
                            NProgress.done();
                            let {
                                meta,
                                data
                            } = res;
                            if (meta.success === false) {
                                this.$notify({
                                    title: '错误',
                                    message: meta.message,
                                    type: 'error'
                                });
                            } else {
                                // this.login(data)
                                // this.$router.addRoutes(routers)
                                // this.$router.push({ path: '/main' });
                                console.log(JSON.stringify(data));
                                sessionStorage.setItem("user_token", JSON.stringify(data));
                                //获取token后 获取菜单
                                getMenuTree().then(
                                    res => {
                                        // console.log(res.data);
                                        this.login(res.data);
                                        this.$router.addRoutes(routers);
                                        this.$router.push({
                                            path: '/main'
                                        });
                                    }
                                )
                            }
                        });
                        this.$router.push({
                            path: '/main'
                        });
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        }
    }
</script>

<style lang="scss" scoped>
    .login-container {
        /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
        -webkit-border-radius: 5px;
        border-radius: 5px;
        -moz-border-radius: 5px;
        background-clip: padding-box;
        margin-bottom: 20px;
        background-color: #F9FAFC;
        margin: 180px auto;
        border: 2px solid #8492A6;
        width: 350px;
        padding: 35px 35px 15px 35px;
        .title {
            margin: 0px auto 40px auto;
            text-align: center;
            color: #505458;
        }
        .remember {
            margin: 0px 0px 35px 0px;
        }
    }
</style>


